import { useTitle } from '../../helpers/custom-hooks.js';
import { useUserProfile } from '../../context/UserContextProvider.js';
import { CONSTANTS } from '../../helpers/constant.js';

const Profile = () => {

	useTitle('Profile');
	const { getUserProfile_c } = useUserProfile();
	const userProfile = getUserProfile_c();

	return (
		<div className="my-8 card">
			<div
				className="card-header border-0 cursor-pointer"
				role="button"
				data-bs-toggle="collapse"
				data-bs-target="#kt_account_profile_details"
				aria-expanded="true"
				aria-controls="kt_account_profile_details"
			>
				<div className="card-title m-0">
					<h3 className="fw-bolder m-0">Profile Details</h3>
				</div>
			</div>
			<div className="card-body border-top p-9">
				<div className="row mb-7">
					<div className="col-1"></div>
					<label className="col-4 fw-bolder text-gray-800">Full Name</label>
					<div className="col-7">
						<span className="fw-bold  fs-6 text-muted">{userProfile?.name}</span>
					</div>
				</div>
				<div className="row mb-7">
					<div className="col-1"></div>
					<label className="col-4 fw-bolder  text-gray-800">CNIC</label>
					<div className="col-7">
						<span className="fw-bold  fs-6  text-muted">{userProfile?.cnic}</span>
					</div>
				</div>
				<div className="row mb-7">
					<div className="col-1"></div>
					<label className="col-4 fw-bolder text-gray-800">Date Of Birth</label>
					<div className="col-7 d-flex align-items-center">
						<span className="fw-bold  fs-6 text-muted me-2">
							{userProfile?.dob?.split("T")[0]}
						</span>
					</div>
				</div>
				<div className="row mb-7">
					<div className="col-1"></div>
					<label className="col-4 fw-bolder text-gray-800">Gender</label>
					<div className="col-7 d-flex align-items-center">
						<span className="fw-bold  fs-6 text-muted me-2">{userProfile?.gender}</span>
					</div>
				</div>
				<div className="row mb-7">
					<div className="col-1"></div>
					<label className="col-4 fw-bolder text-gray-800">
						Phone
						{/* <i
              className="fas fa-exclamation-circle ms-1 fs-7"
              data-bs-toggle="tooltip"
              title=""
              data-bs-original-title="Phone number must be active"
              aria-label="Phone number must be active"
            ></i> */}
					</label>
					<div className="col-7 d-flex align-items-center">
						<span className="fw-bold  fs-6  text-muted me-2">{userProfile?.phone}</span>
					</div>
				</div>
				<div className="row mb-7">
					<div className="col-1"></div>
					<label className="col-4 fw-bolder text-gray-800">Email</label>
					<div className="col-7 d-flex align-items-center">
						<span className="fw-bold  fs-6 text-muted me-2">{userProfile?.email}</span>
					</div>
				</div>
				<div className="row mb-7">
					<div className="col-1"></div>
					<label className="col-4 fw-bolder text-gray-800">Role</label>
					<div className="col-7 d-flex align-items-center">
						<span className="fw-bold  fs-6 text-muted me-2">{userProfile?.role === CONSTANTS.ADMIN ? CONSTANTS.RESEARCH_ASSISTANT : CONSTANTS.DATA_COLLECTOR}</span>
					</div>
				</div>
				{/* <div className="row">
					<div className="col-1"></div>
					<button
						onClick={() => editProfile()}
						className="btn btn-primary btn-sm fw-bolder  col-3 "
						type="button"
					>
						Change Password
					</button>
				</div> */}
			</div>
		</div>
	);
};
export default Profile;
